@import url(https://fonts.googleapis.com/css2?family=Besley:ital,wght@0,400;0,500;1,400;1,500&display=swap);
html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:"";content:none}table{border-collapse:collapse;border-spacing:0}a{text-decoration:none}#root,body,html{height:100%;width:100%;font-family:"Besley",serif}

header{width:100%;padding:1.5rem 0;background-color:#fff}header .header-container{width:75%;margin:auto;display:grid;grid-template-columns:1fr auto auto;align-items:center;grid-column-gap:2rem;-webkit-column-gap:2rem;column-gap:2rem}header .header-container div.logo img{width:200px}header .header-container nav a{border-bottom:3px solid transparent;padding-bottom:1rem;letter-spacing:0.04rem;margin-left:2rem;font-size:1rem;color:#102757;cursor:pointer;transition:all 0.5s;font-weight:600}header .header-container nav a svg{margin-right:0.5rem;display:inline-block;color:rgba(0,0,0,0.5)}header .header-container nav a:hover{border-color:#00bcd4}header .header-container nav a:hover svg{color:#00bcd4}header .header-container div.navigation a{transition:all 0.5s;display:block;background-color:#102757;width:2.4rem;height:2.4rem;border-radius:50%;padding:0.7rem;box-sizing:border-box;text-align:center}header .header-container div.navigation a svg{color:#ffffff}header .header-container div.navigation a:hover{background-color:#00bcd4}

.slide-container img{width:100%}

section.home_component{text-align:center;background-color:#f5f5f5;padding:5rem 0}section.home_component div.container{width:75%;margin:auto}section.home_component div.container h1{font-size:2rem;font-weight:bolder;letter-spacing:0.1rem;text-transform:uppercase;color:#102757}section.home_component div.container h2{font-style:italic;letter-spacing:0.1rem;font-size:1.5rem;margin:1rem 0 2rem 0;color:#00bcd4}section.home_component div.container p{font-size:1.1rem;letter-spacing:0.06rem;line-height:2rem}

section.service_component div.container div.item{display:grid;grid-template-columns:1fr 1fr}section.service_component div.container div.item:nth-child(odd) article{grid-column:1}section.service_component div.container div.item:nth-child(odd) div.background{grid-column:2;grid-row:1}section.service_component div.container div.item div.background{background-size:cover;background-position:center center}section.service_component div.container div.item article{padding:6rem 8rem}section.service_component div.container div.item article h1{font-size:1.5rem;font-weight:bolder;letter-spacing:0.1rem;text-transform:uppercase;color:#102757}section.service_component div.container div.item article div.separator{background-color:#00bcd4;display:inline-block;width:3rem;height:0.3rem;margin:2rem 0 3rem 0}section.service_component div.container div.item article p{font-size:1.1rem;letter-spacing:0.06rem;line-height:2rem}

